/* FONTS */
/* COLOURS */
/* MEDIA QUERIES */
/* BASE */
* {
  box-sizing: border-box;
}
h1 {
  font-size: 2rem;
}
h2 {
  font-size: 2.5rem;
}
h3 {
  font-size: 1.5rem;
}
ul {
  list-style-type: none;
}
section,
header {
  padding: 1.5rem;
  max-width: 25em;
}
/* GRID */
.flex-grid {
  display: flex;
}
.col-4-lg {
  flex: 4;
}
.col-8-lg {
  flex: 8;
}
@media only screen and (min-width: 601px) and (max-width: 1100px) {
  .col-8-lg {
    flex: 6;
  }
}
.main-container {
  background-image: url("../images/bg-mobile-header.png");
  background-size: 100%;
  background-repeat: no-repeat;
}
@media only screen and (max-width: 600px) {
  .main-container {
    margin: 0 auto;
    max-width: 400px;
  }
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .main-container {
    margin: 0 auto;
    max-width: 2000px;
    background: none;
  }
}
.content-left {
  margin-left: auto;
}
@media only screen and (min-width: 1101px) {
  .content-left section,
  .content-left header {
    margin-left: auto;
  }
}
/* HEADER */
.site-header {
  height: 270px;
}
@media only screen and (min-width: 1101px) {
  .site-header {
    height: initial;
    background-image: none;
  }
}
.site-title {
  width: 175px;
  font-family: 'Lato', sans-serif;
  text-transform: uppercase;
  padding-top: 90px;
}
.site-title--a {
  color: #959595;
}
.site-title--b {
  color: #c1c1c1;
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .site-title {
    padding-top: 45px;
    font-size: 3rem;
    width: initial;
  }
}
@media only screen and (min-width: 1600px) {
  .site-title {
    font-size: 4.1rem;
  }
  .site-title--a {
    margin-left: -125px;
  }
  .site-title--b {
    width: 400px;
  }
}
@media only screen and (min-width: 1101px) {
  .content-left .site-title {
    text-align: right;
  }
  .content-left .site-title--b {
    display: none;
  }
}
.main-sidebar .site-header {
  color: #fff;
  padding-left: 0;
}
.main-sidebar .site-header .site-title {
  position: relative;
  left: 0;
}
.main-sidebar .site-header .site-title--b {
  color: #fff;
}
@media only screen and (max-width: 600px), only screen and (min-width: 601px) and (max-width: 1100px) {
  .main-sidebar .site-header {
    display: none;
  }
}
@media only screen and (min-width: 1101px) {
  .main-sidebar .site-header .site-title--a {
    display: none;
  }
}
/* BODY */
.main-body .logo {
  padding: 0 0 2rem 0;
}
.main-body p {
  font-family: 'Raleway', sans-serif;
  color: #959595;
  margin-bottom: 1em;
  line-height: 1.5em;
}
.main-body span {
  color: #00b3ea;
}
/* SIDEBAR */
.main-sidebar {
  background: url("../images/background.jpg") no-repeat;
  background-size: cover;
  background-position: -50px 0;
  padding: 0 1.5rem;
  height: 100vh;
}
@media only screen and (max-width: 600px) {
  .main-sidebar {
    display: none;
  }
}
/* BUTTONS */
.button {
  font-size: 1.125rem;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: #00b3ea;
  padding: 0.75em 1em;
  background-color: transparent;
  text-align: right;
  display: inline-block;
  border: 1px solid #00b3ea;
  width: 100%;
  text-decoration: none;
}
.button:before {
  content: "";
  display: inline-block;
  float: left;
}
.button--vision:before {
  background-image: url("../images/icons/icon_play.png");
  width: 18px;
  height: 18px;
}
.button--contact:before {
  background-image: url("../images/icons/icon_mail.png");
  width: 16px;
  height: 12px;
  margin-top: 3px;
}
.button--contact {
  display: none;
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .button-group {
    display: flex;
  }
  .button-group .button {
    margin-right: 1em;
    font-size: 0.75rem;
    line-height: 1.5em;
    max-width: 136px;
  }
  .button-group .button--contact {
    display: block;
  }
}
/* SOCIAL */
.social-section .list--social {
  display: flex;
  justify-content: space-between;
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .social-section--mobile {
    display: none;
  }
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .social-section--large {
    position: absolute;
    padding-top: 180px;
    top: 20%;
    width: 20rem;
  }
}
/* ICONS */
.icon--social svg {
  height: 4.375rem;
}
.icon--social svg .cls-1 {
  transition: 200ms;
  transition-timing-function: ease-inout;
}
.icon--social svg:hover .cls-1 {
  fill: #0f414c;
}
.main-sidebar .icon .cls-1 {
  fill: #0f414c;
}
.main-sidebar .icon:hover .cls-1 {
  fill: #00b3ea;
}
/* SERVICES */
.services {
  font-family: 'Passion One', 'Impact', sans-serif;
  font-size: 1.5rem;
  text-transform: uppercase;
  color: #959595;
  margin-top: 2.5rem;
}
.services li {
  padding-left: 0.5em;
  margin-bottom: 0.25em;
}
.services li:nth-child(1) {
  border-left: 8px solid #f49436;
}
.services li:nth-child(2) {
  border-left: 8px solid #d40b8a;
}
.services li:nth-child(3) {
  border-left: 8px solid #8ec966;
}
.services li:nth-child(4) {
  border-left: 8px solid #b31f43;
}
/* CONTACT US */
.contact {
  font-family: 'Raleway', sans-serif;
  margin-bottom: 1em;
}
.contact h2 {
  font-family: 'Lato', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: #7f7f7f;
  margin-bottom: 0.5em;
}
.contact a {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  text-decoration: none;
  color: #00b3ea;
  line-height: 1.75em;
}
.address a {
  font-size: 1.25rem;
  font-family: 'Raleway', sans-serif;
  line-height: 1.75em;
  color: #959595;
  text-decoration: none;
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .content-left .contact-us {
    display: none;
  }
}
/* FOOTER */
.site-footer {
  height: 130px;
  overflow: hidden;
  font-family: 'Raleway', sans-serif;
}
.site-footer .copyright {
  color: #00b3ea;
}
.site-footer-image {
  border-top: 6px solid #00b3ea;
  background-image: url("../images/bg-mobile-footer.jpg");
  background-size: cover;
  height: 300px;
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (min-width: 1101px) {
  .site-footer-image {
    display: none;
  }
}
/* VIMEO MODAL */
.modal--container {
  position: absolute;
  height: 0;
  width: 100%;
  padding-bottom: 56.25%;
  visibility: hidden;
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.5s ease-in-out;
  z-index: 10;
}
.modal:before {
  content: "";
  position: fixed;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(255,255,255,0.95);
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transition: opacity 0.5s ease-in-out;
  z-index: 10;
}
.modal:target .modal--container,
.modal:target:before {
  display: block;
  visibility: visible;
  opacity: 1;
}
.modal--close {
  position: absolute;
  top: -23px;
  right: 0;
  padding: 0;
  color: #00b3ea;
  font-family: 'Raleway', sans-serif;
  font-size: 0.75rem;
  text-decoration: none;
  z-index: 10;
  border: 1px solid #00b3ea;
  padding: 0.5em;
  text-align: center;
  line-height: 0.78;
}
.video-modal iframe,
.video-modal object,
.video-modal embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media only screen and (min-width: 1101px) {
  .video-modal .modal--container {
    width: 960px;
    height: 538px;
    padding-bottom: 0;
  }
}
@media only screen and (min-width: 601px) and (max-width: 1100px), only screen and (max-width: 600px) {
  .video-modal .modal--close {
    top: initial;
    bottom: -22px;
  }
}
.contact-modal {
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 10;
}
.contact-modal h2 {
  font-size: 2.5rem;
}
.contact-modal .contact a {
  font-size: 1.5rem;
}
.contact-modal .address a {
  font-size: 1rem;
}
.contact-modal .modal--container {
  width: 20rem;
  height: 20rem;
}
.contact-modal .modal--close {
  top: -20px;
  right: inherit;
  left: 17rem;
}
@media only screen and (min-width: 601px) and (max-width: 840px) {
  html {
    font-size: 13px;
  }
}
